import React, { Component } from 'react'
import { Modal } from 'antd';
import * as ApiGithub from '../../API/ApiGithub'
import moment from 'moment'
import Chronictablecelltooltip from '../ChronicleView/ChronicTableCellTooltipCell'
import NonLabReport from '../CrossSectionView/NonLabReport'

export default class InfectionlabtableCell extends Component {
    state = {
        SummaryParse: [],
        visible: false,
        detailMo: {},
        showDetail: false,
    }

    componentDidMount() {
        ApiGithub.getGitHubJson("Summary.json")
            .then(data => {
                this.setState({
                    SummaryParse: data.data
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    CellText() {
        const { element, caseinfo } = this.props
        const { showDetail, summaryParse } = this.state
        // const start = moment(element.StartTime).format("YYYY/MM/DD HH:mm:ss")
        const startTimeStamp = moment(element.StartTime).valueOf()
        // const end = moment(element.EndTime).format("HH:mm:ss")
        const title = ["採檢時間", "檢驗項目", "檢體", "報告"]
        const titleCell = title.map(function (value, index) {
            return (
                <div key={index} style={{ display: "flex", justifyContent: "center", alignItems: "center", padding: "0px 5px" }}>
                    {value}
                </div>
            );
        })
        const objectElementCell = element.map((info, index, arr) => {
            return (
                <div key={index}>
                    <Chronictablecelltooltip startTimeStamp={startTimeStamp} tooltipelement={info.Data} caseinfo={caseinfo} summaryParse={summaryParse} parentDateCallback={this.callbackDateFunction}></Chronictablecelltooltip>
                </div>
            );
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "1.5fr 2fr", gridColumnGap: "20px" }}>
                <div>
                    <div style={{ fontSize: "14px", fontWeight: "500" }}>檢驗清單</div>
                    <div style={{ height: "30px", display: "grid", gridTemplateColumns: "1fr 2fr 1fr 1fr", borderRadius: "4px 4px 0 0 ", backgroundColor: "rgba(226, 237, 243,.5)", justifyItems: "center", marginTop: "10px" }}>
                        {titleCell}
                    </div>
                    <div style={{ border: "1px solid rgba(232, 232, 232, 1)", borderRadius: "0 0 4px 4px", borderCollapse: "collapse" }}>
                        {objectElementCell}
                    </div>
                </div>
                <div>
                    <div style={{ fontSize: "14px", fontWeight: "500" }}>檢驗報告</div>
                    <div style={{ height: "500px", borderRadius: "4px 4px 4px 4px", border: "2px solid rgba(235, 235, 235)", marginTop: "10px" }}>
                        {showDetail ? <NonLabReport detail={this.state.detailMo} ></NonLabReport>
                            :
                            <div>
                                請選擇檢驗清單任一項目之報告「顯示」項，此項目之檢驗報告內容將在此呈現。
                             </div>
                        }
                    </div>
                </div>
            </div>
        );
    }
    callbackDateFunction = (event) => {
        this.setState({
            detailMo: event,
            showDetail: true
        });
    }
    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
            showDetail: false
        });
    };

    render() {
        function valueFormat(value) {
            if (value === 0 || value === undefined || value === null || value === "") {
                return ""
            }
            return `${value}`
        }
        const { element, borderleftColor } = this.props
        const TitleRender = this.CellText()
        if (Array.isArray(element)) {
            return (
                <div style={{ cursor: "pointer", backgroundColor: 'rgba(0,120,230,.1)', height: '40px', display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse', borderLeft: borderleftColor }}>
                    <Modal
                        title="檢驗資料"
                        visible={this.state.visible}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        width={1200}
                        footer={null}>
                        {TitleRender}
                    </Modal>
                    <div onClick={this.showModal}>
                        {valueFormat(element.length) + "筆"}
                    </div>
                </div>
            );
        }
        return null
    }
}
