import React, { Component } from 'react';
import { Button, DatePicker, TimePicker, InputNumber, Icon, message } from 'antd';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'

import VitalLimitValueRow from './VitalLimitValueRow'
import './Vitalsign.scss'


export default class VitalLimitValue extends Component {

  state = {
    VSLimitRange: []
  }
  componentDidMount = () => {
    this.getVitalSignLimitRange()
  }

  componentDidUpdate = () => {
    // this.getVitalSignRange()
  }

  getVitalSignLimitRange = () => {
    const id = this.props.caseinfo.HISID.toString().trim()
    ApiProvider.findLimitValue(id)
      .then(data => {
        if (data.data.length <= 0) {
          //預設Y值上下限
          const defaultData = [
            {
            }
          ];

          this.setState({
            VSLimitRange: defaultData,
          })
        } else {
          this.setState({
            VSLimitRange: data.data,
          })
        }
      })
      .catch(err => { console.log(err) })
  }

  render() {
    const { VSLimitRange } = this.state
    const title = ["HR", "BP", "RR", "SPO2", "BT"]
    const titleCellRender = title.map(function (element, index, arr) {
      return (
        <div key={index} style={{ display: "flex", alignItems: "center", borderCollapse: "collapse", paddingLeft: "10px" }}>
          {element === "SPO2" ?
            <div style={{ fontSize: "14px" }}>SpO
              <sub>2</sub>
            </div>
            :
            <div style={{ fontSize: "14px" }}>{element}</div>}
        </div>
      );
    })

    const id = this.props.caseinfo.HISID.toString().trim()
    const limitValue = VSLimitRange.map((element, index, arr) => {
      return (
        <VitalLimitValueRow key={index} element={element} index={index} id={id} callBack={this.getVitalSignLimitRange}></VitalLimitValueRow>
      );
    });

    return (
      <div style={{ height: "500px" }}>
        <div style={{ marginTop: "10px" }}>
          <div style={{ height: "45px", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr 0.5fr", backgroundColor: "rgba(226, 237, 243,.5)" }}>
            {titleCellRender}
          </div>
          <div style={{ minHeight: "120px", maxHeight: "500px", overflow: "auto" }}>
            {limitValue}
          </div>
        </div>
      </div>
    );
  }
}
