import React, { Component } from 'react';
import { DatePicker, Select, InputNumber, Icon, message } from 'antd';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'

import './Vitalsign.scss'

export default class VitalLimitValueFix extends Component {
  
  saveData = () => {
    const { fixStatus } = this.props
    let database = undefined
    let defaultY = undefined
    if (fixStatus === true) {
      database = this.props.element
      defaultY = this.props.defaultY
    }
    const saveData = {
      'hisid':this.props.id,
      'hr_high': parseInt(document.querySelector('#l_hrhi').value, 10),
      'hr_low': parseInt(document.querySelector('#l_hrlo').value, 10),
      'bp_high': parseInt(document.querySelector('#l_bphi').value, 10),
      'bp_low': parseInt(document.querySelector('#l_bplo').value, 10),
      'rr_high': parseInt(document.querySelector('#l_rrhi').value, 10),
      'rr_low': parseInt(document.querySelector('#l_rrlo').value, 10),
      'spo2_high': parseInt(document.querySelector('#l_sphi').value, 10),
      'spo2_low': parseInt(document.querySelector('#l_splo').value, 10),
      'bt_high': parseInt(document.querySelector('#l_bthi').value, 10),
      'bt_low': parseInt(document.querySelector('#l_btlo').value, 10),
    }
    //沒值帶入預設值
    if(isNaN(saveData.hr_high)) saveData.hr_high = defaultY.hr_high
    if(isNaN(saveData.hr_low)) saveData.hr_low = defaultY.hr_low
    if(isNaN(saveData.bp_high)) saveData.bp_high = defaultY.bp_high
    if(isNaN(saveData.bp_low)) saveData.bp_low = defaultY.bp_low
    if(isNaN(saveData.rr_high)) saveData.rr_high = defaultY.rr_high
    if(isNaN(saveData.rr_low)) saveData.rr_low = defaultY.rr_low
    if(isNaN(saveData.spo2_high)) saveData.spo2_high = defaultY.spo2_high
    if(isNaN(saveData.spo2_low)) saveData.spo2_low = defaultY.spo2_low
    if(isNaN(saveData.bt_high)) saveData.bt_high = defaultY.bt_high
    if(isNaN(saveData.bt_low)) saveData.bt_low = defaultY.bt_low

    if (saveData.hr_high <= saveData.hr_low) {
      message.error('HR value is error', 1);
    }
    else if (saveData.bp_high <= saveData.bp_low) {
      message.error('BP value is error', 1);
    }
    else if (saveData.rr_high <= saveData.rr_low) {
      message.error('RR value is error', 1);
    }
    else if (saveData.spo2_high <= saveData.spo2_low) {
      message.error('SPO2 value is error', 1);
    }
    else if (saveData.bt_high <= saveData.bt_low) {
      message.error('BT value is error', 1);
    }else{
      ApiProvider.createLimitValue(saveData)
      .then(data => {
        this.props.review()
        this.props.cancelFix()
      }).catch(err => { console.log(err) })
    }
  }

  cancelFix = () =>{
    this.props.cancelFix()
  }

  render() {
    const { fixStatus } = this.props
    let database = undefined
    let defaultY = undefined
    if (fixStatus === true) {
      database = this.props.element
      defaultY = this.props.defaultY
    }

    return (
      <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr 0.5fr", border: "1px solid rgba(232, 232, 232, 1)", borderTop: "0px", alignContent: "center", height: "60px" }}>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="l_hrhi" size="small" defaultValue={fixStatus ? database.hr_high : null} placeholder={defaultY.hr_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="l_hrlo" size="small" defaultValue={fixStatus ? database.hr_low : null}  placeholder={defaultY.hr_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="l_bphi" size="small" defaultValue={fixStatus ? database.bp_high : null}  placeholder={defaultY.bp_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="l_bplo" size="small" defaultValue={fixStatus ? database.bp_low : null} placeholder={defaultY.bp_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="l_rrhi" size="small" defaultValue={fixStatus ? database.rr_high : null} placeholder={defaultY.rr_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="l_rrlo" size="small" defaultValue={fixStatus ? database.rr_low : null} placeholder={defaultY.rr_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="l_sphi" size="small" defaultValue={fixStatus ? database.spo2_high : null} placeholder={defaultY.spo2_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="l_splo" size="small" defaultValue={fixStatus ? database.spo2_low : null} placeholder={defaultY.spo2_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr", fontSize: "14px", paddingLeft: "10px", alignContent: "center" }}>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto" }}>
            <div style={{ width: "50px" }}>Hight :</div>
            <div className="inputset">
              <InputNumber id="l_bthi" size="small" defaultValue={fixStatus ? database.bt_high : null} placeholder={defaultY.bt_high} min={0} max={888} />
            </div>
          </div>
          <div style={{ display: "grid", gridTemplateColumns: "50px auto", marginTop: "1px" }}>
            <div>Low :</div>
            <div className="inputset">
              <InputNumber id="l_btlo" size="small" defaultValue={fixStatus ? database.bt_low : null} placeholder={defaultY.bt_low} min={0} max={888} />
            </div>
          </div>
        </div>
        <div className="checkButton">
          <Icon type="save" style={{ fontSize: "26px" }} onClick={this.saveData} />
          <Icon type="close" style={{ fontSize: "26px" }} onClick={this.cancelFix} />
        </div>
      </div >
    );
  }
}
