import React, { Component } from 'react';
import { DatePicker } from 'antd';
import { Select } from 'antd';
import './Calender.scss'
import { mmddformat, compareday, yyyymmfirstweeksate } from '../../Function/Timeformat'
import Calendercell from './Calendercell'
import { Icon } from 'antd'
import CrossectionSchedule from '../CrossSectionView/Crosssectionschedule'
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment';

const SCHEDULE_TYPE_EVENT = "事件";
// const SCHEDULE_TYPE_SCHEDULE = "排程";
const timeZoneOffset = 8 * 60 * 60 * 1000;

class Calender extends Component {
    state = {
        calendermode: 'week',
        Clickdays: null,
        ClickDate: null,
        displayinfo: null,
        scheduleRawData: [],
        scheduleData: [],
        startTimestamp: moment().valueOf(),
        endTimestamp: 0,
        todayBegin: 0,
        noticeRawData: [],
        open: false,
        reCalender: false,


    }
    componentDidUpdate() {
        if (this.state.reCalender) { this.setState({ reCalender: false }) }
    }

    callbackFunction = (timestamp, clickindex) => {
        // console.log(timestamp)
        this.setState({
            ClickDate: timestamp,
            displayinfo: clickindex
        })
    }
    onCancelClick() {
        this.setState({
            ClickDate: this.state.todayBegin,
            displayinfo: null
        }, () => { console.log(this.state) })
    }
    CancelModule = (value) => {

        if (value === true) {
            this.setState({ open: true }, () => { console.log("oepn true", this.state) })
        } else {
            this.setState({ open: false }, () => { console.log("oepn false", this.state) })
        }
    }
    handleChange = (value) => {
        this.setState({
            calendermode: value,
            displayinfo: null,
            startTimestamp: moment().valueOf()
        })
    }
    onChange = (timestamp) => {
        console.log('changestate')
    }
    onClickCheck(timestampdate) {
        this.setState({
            onClickdays: timestampdate
        })
    }
    getDataFromTimestamp(timestamp) {
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const dataOfDayIndex = this.state.scheduleData.findIndex(element => element.time === dateStr);
        const dataOfDay = (dataOfDayIndex === -1) ? { scheduleList: [] } : this.state.scheduleData[dataOfDayIndex];
        return dataOfDay;
    }
    getNoticeFromTimestamp(timestamp) {
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const noticeOfDayArray = this.state.noticeRawData.filter(element => element.time.indexOf(dateStr) > -1);
        return noticeOfDayArray;
    }
    weekmode() {
        var weekcontext = []
        for (let index = -3; index <= 3; index++) {
            const time = this.state.startTimestamp
            const weekStart = moment(time).add(index, 'd').valueOf()
            const date = moment(weekStart).format("MM/DD")
            // 接 len api
            weekcontext.push(
                <Calendercell caseinfo={this.props.caseinfo} CancelModule={this.CancelModule} clickindex={'week'} clickdate={this.state.ClickDate} timestamp={weekStart} date={date} state={compareday(weekStart)} newDate={false} today={this.state.todayBegin} parentCallback={this.callbackFunction} reCalender={this.state.reCalender}></Calendercell>
            );
        }
        return weekcontext
    }
    weekmonthmode(weeknumber, weekStart) {
        var weekcontext = []
        //clickindex, state, newDate, timestamp, clickdate, today
        for (let index = 0; index < 7; index++) {
            const timestampdate = moment(weekStart).add(index, 'd').valueOf();
            const date = moment(timestampdate).format("MM/DD")
            if (moment().startOf("day").valueOf() <= timestampdate) {
                weekcontext.push(
                    <Calendercell caseinfo={this.props.caseinfo} CancelModule={this.CancelModule} clickindex={weeknumber} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={true} newDate={true} today={this.state.todayBegin} parentCallback={this.callbackFunction}>
                    </Calendercell>
                );
            }
            else {
                weekcontext.push(
                    <Calendercell caseinfo={this.props.caseinfo} CancelModule={this.CancelModule} clickindex={weeknumber} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={false} newDate={true} today={this.state.todayBegin} parentCallback={this.callbackFunction}>
                    </Calendercell>
                );
            }
        }
        return weekcontext
    }

    reCalenderStyle = () => {
        this.setState({
            reCalender: true
        })
    }

    ShowModule(rowIndex) {
        if (rowIndex === undefined) rowIndex = 0;
        // console.log("this.state.ClickDate" + this.state.ClickDate);

        const dataOfDay = this.getDataFromTimestamp(this.state.ClickDate);
        const noticeOfDay = this.getNoticeFromTimestamp(this.state.ClickDate);

        function checkmode(state) {
            if (state === 'week') {
                return 'calenderdisplayinformation'
            }
            if (state === rowIndex) {
                return 'calenderdisplayinformation'
            }
            return 'calenderdisplayinformationnone'
        }
        return (
            <div>
                {this.state.open ?
                    <div className={checkmode(this.state.displayinfo)} >

                        <div className='calenderModule'>
                            <t1>詳細資訊</t1>
                        </div>
                        <div className='Modulelayout'>
                            <CrossectionSchedule reCalender={this.reCalenderStyle} PickerDate={this.state.ClickDate} source={1} caseinfo={this.props.caseinfo} noticeData={noticeOfDay} scheduleData={dataOfDay} editstatus={true}></CrossectionSchedule>
                        </div>
                    </div>
                    : ""}
            </div>
        );
    }


    setgroup() {
        var calendermonthgroup = []
        for (let index = 0; index < 5; index++) {
            const weekStart = moment(this.state.startTimestamp).startOf("month").startOf("week").add(index, 'week').valueOf()
            calendermonthgroup.push(
                <div className='calendercontextMonthRows'>
                    {this.weekmonthmode(index, weekStart)}
                    {this.ShowModule(index)}
                </div>
            );

        }
        return calendermonthgroup
    }
    modechange(state) {
        if (state === 'week') {
            return (
                <div className='calendercontext'>
                    {this.weekmode()}
                    {this.ShowModule()}
                </div>
            );
        }
        else {
            return this.setgroup()
        }
    }




    SelectDatepickertype() {
        const { MonthPicker } = DatePicker;
        if (this.state.calendermode === 'month') {
            return (
                <MonthPicker format={'YYYY/MM'} getCalendarContainer={triggerNode => triggerNode.parentNode} placeholder="請選擇月份" onChange={(date, dateString) => { this.setState({ startTimestamp: date.valueOf() }) }} allowClear={false} />
            );
        }
        else {
            return (
                <DatePicker format={'YYYY/MM/DD'} getCalendarContainer={triggerNode => triggerNode.parentNode} placeholder={['請選擇時間']} onChange={(date, dateString) => { this.setState({ startTimestamp: date.valueOf() }) }} allowClear={false} />
            );
        }
    }
    render() {
        const { Option } = Select;
        return (
            <div>
                <t1 className='calendertitle'>
                    <t2 style={{ fontSize: "16px", color: "rgba(0,0,0,.7)" }}>行事曆</t2>
                </t1>
                <div className='calendercontrol'>
                    {this.SelectDatepickertype()}
                    <Select defaultValue={this.state.calendermode} onChange={this.handleChange}>
                        <Option value="week">週</Option>
                        <Option value="month">月</Option>
                    </Select>
                </div>
                {this.modechange(this.state.calendermode)}
            </div>
        );
    }
}

export default Calender
